const reducer = (state, action) => {
    switch (action.type) {
        case "GET_DATA":
            return {
                ...state,
                data: action.payload
            }
            break;
        case "GET_DOCUMENTOS":
            return {
                ...state,
                documentos: action.payload
            }
            break;
        case "RECUPERANDO_USUARIO":
            return {
                ...state,
                recuperarUsuario: action.payload
            }
            break;
        case "IS_LOADING":
            return {
                ...state,
                loading: action.payload
            }
            break;
        case "CHANGE_USER":
            return {
                ...state,
                user: action.payload
            }
            break;
        case "CHANGE_CLIENTE":
            return {
                ...state,
                cliente: action.payload
            }
            break;
        case "CHANGE_PROVEEDOR":
            return {
                ...state,
                proveedor: action.payload
            }
            break;
        case "CHANGE_EXPRESO":
            return {
                ...state,
                expreso: action.payload
            }
            break;
        default:
            return state
            break;
    }
}
export default reducer