import React, { Component } from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import "./styles/Product.css"
import * as firebase from "firebase"
import { connect } from "react-redux"

class Product extends Component {
    async sumar(nombre, cantidad) {
        const nuevaCantidad = parseInt(prompt("Ingresar cantidad a sumar"))
        await this.sumarArticulo(nombre, cantidad, nuevaCantidad, (this.props.seccion + "2"))
        if (nombre === "Buje 3 Pulg" || nombre === "Buje 4 Pulg" || (nombre === "Valvula De Pie" || nombre === "Valvula Bajo Surtidor" || nombre === "Valvula Bajo Surtidor 2 Pulg" || nombre === "Tapa 3 Pulg")) {
            if (this.props.seccion === "productos") {
                if (nombre === "Buje 3 Pulg" || nombre === "Buje 4 Pulg") {
                    await this.descontarArticulo(nombre, this.props.noMecanizados[nombre].cantidad, nuevaCantidad, "no-mecanizados2")
                }
                else {
                    if (nombre === "Tapa 3 Pulg") {
                        await this.descontarArticulo(nombre, this.props.mecanizados[nombre].cantidad, nuevaCantidad, "mecanizados2")
                        await this.descontarArticulo("Gatillos", this.props.mecanizados["Gatillos"].cantidad, nuevaCantidad, "mecanizados2")
                    }
                    else {
                        await this.descontarArticulo(nombre, this.props.mecanizados[nombre].cantidad, nuevaCantidad, "mecanizados2")
                    }
                }
            }
            else if (this.props.seccion === "mecanizados") {
                try {
                    await this.descontarArticulo(nombre, this.props.noMecanizados[nombre].cantidad, nuevaCantidad, "no-mecanizados2")
                }
                catch{
                }
            }
        }
    }
    async sumarArticulo(nombre, cantidadActual, nuevaCantidad, seccion) {
        await firebase.database().ref().child(seccion).child(nombre).update({
            cantidad: (cantidadActual + nuevaCantidad)
        })
    }
    async descontarArticulo(nombre, cantidadActual, nuevaCantidad, seccion) {
        console.log(cantidadActual)
        await firebase.database().ref().child(seccion).child(nombre).update({
            cantidad: (cantidadActual - nuevaCantidad)
        })
    }
    render() {
        return (
            <div className="container product bg-dark">
                <div className="row">
                    <div className="container img">
                        <div className="row">
                            <img src={this.props.imagen} className="productImg" alt="" />

                        </div>
                    </div>
                    <div className="col bodyProduct">
                        <div className="container">
                            <div className="row">
                                <div className="col-12 mt-2 text-center">
                                    <h5 className="colorWhite">{this.props.nombre}</h5>
                                </div>
                            </div>
                            <div className="row">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-6 text-left">
                                            <h5 className="colorWhite">Cantidad</h5>
                                        </div>
                                        <div className="col-6 text-right">
                                            <h5><span className="badge badge-light">{this.props.cantidad}</span></h5>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-6 text-left">
                                            <h5 className="colorWhite">Precio</h5>
                                        </div>
                                        <div className="col-6 text-right">
                                            <h5><span className="badge badge-light">$ {this.props.precio}</span></h5>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row mt-2">
                    <div className="col-6 form-group text-center">
                        <button type="button" className="btn btn-outline-light btnProduct" onClick={async () => {
                            await this.sumar(
                                this.props.nombre,
                                this.props.cantidad
                            )
                        }}>
                            Agregar Cantidad!
                                            </button>
                    </div>
                    <div className="col-6 form-group text-center">
                        <button type="button" className="btn btn-outline-light btnProduct" onClick={async () => {
                            const nuevoPrecio = prompt("Ingresar el nuevo precio del producto")
                            await firebase.database().ref().child(this.props.seccion + "2").child(this.props.nombre).update({
                                precio: parseInt(nuevoPrecio)
                            })
                        }}>
                            Modificar Precio!
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state => {
    return {
        products: state.data["productos2"],
        mecanizados: state.data["mecanizados2"],
        noMecanizados: state.data["no-mecanizados2"],
    }
}
export default connect(mapStateToProps, null)(Product)