import React, { Component } from "react"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import "./Styles/ChequesList.css"

import * as firebase from "firebase"

class ListasDePrecios extends Component {
    state = {
        loading: true,
        edit: false,
        documentos: [],
    }
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    async fetchData() {
        const listasRef = await firebase.storage().ref().child('ListasDePrecios')
        let listasArr = await listasRef.listAll()
        listasArr.items.map(lista => {
            lista.getDownloadURL().then(url => {
                this.setState({
                    documentos: {
                        ...this.state.documentos,
                        [lista.name]: url
                    }
                })
            })
        })
        this.setState({
            loading: false
        })
    }
    async subirDocumento() {
        if (this.state.file) {
            console.log(this.state.file)
            await firebase.storage().ref().child("ListasDePrecios").child(this.state.file.name).put(this.state.file).then(e => {
                this.setState({
                    file: undefined,
                    documentos: undefined,
                    loading: true
                })
                this.fetchData()
            })
        }
    }
    async borrarDocumento(documento) {
        confirmAlert({
            title: 'Eliminar Documento?',
            buttons: [
                {
                    label: 'Si, Eliminar',
                    onClick: async () => {
                        await firebase.storage().ref().child("ListasDePrecios").child(documento).delete().then(e => {
                            this.setState({
                                documentos: undefined,
                                edit: false,
                                loading: true
                            })
                            this.fetchData()
                        }
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    async componentDidMount() {
        this.fetchData()
    }
    render() {
        try {
            const user = this.state.user.email
            if (user) {
                if (this.state.loading) {
                    return (
                        <div>
                            <SpinnerLoading />
                        </div>
                    )
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-12  col-md-8 offset-md-2 col-lg-6 offset-lg-3 form-group">
                                            <div class="input-group mb-3">
                                                <div class="input-group-prepend">
                                                    {this.state.file ?
                                                        <button type="button" className="btn btn-success" onClick={e => {
                                                            this.subirDocumento()
                                                        }}>Subir Documento!</button>
                                                        :
                                                        <button type="button" className="btn btn-success disabled">Subir Documento!</button>
                                                    }
                                                </div>
                                                <div class="custom-file">
                                                    <input type="file" class="custom-file-input" id="inputGroupFile01" onChange={e => {
                                                        this.setState(
                                                            {
                                                                file: e.target.files[0]
                                                            }
                                                        )
                                                    }} />
                                                    <label class="custom-file-label" for="inputGroupFile01">{
                                                        this.state.file ?
                                                            "Archivo Elegido!"
                                                            :
                                                            "Elegir Archivo"
                                                    }</label>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-12 form-group text-center">
                                            <button type="button" className="btn btn-danger" onClick={e => {
                                                if (this.state.edit) {
                                                    this.setState({
                                                        edit: false
                                                    })
                                                }
                                                else {
                                                    this.setState({
                                                        edit: true
                                                    })
                                                }
                                            }}>Eliminar Archivo</button>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.state.documentos).map(nombre => (
                                            (this.state.edit ?
                                                <div className="col-12 form-group text-center">
                                                    <button type="button" className="btn btn-outline-danger" onClick={e => {
                                                        this.borrarDocumento(nombre)
                                                    }}>{nombre.slice(0, nombre.indexOf("."))}</button>
                                                </div>
                                                :
                                                <div className="col-12 form-group text-center">
                                                    <button type="button" className="btn btn-outline-light" onClick={e => {
                                                        window.open(this.state.documentos[nombre])
                                                    }}>{nombre.slice(0, nombre.indexOf("."))}</button>
                                                </div>
                                            )
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>

                    )
                }
            }
        }
        catch{
            return (
                <Login redirect="ListasDePrecios" />
            )
        }
    }
}
export default ListasDePrecios