import React, { Component } from "react"
import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"
import NavDropdown from "react-bootstrap/NavDropdown"
import { Link } from "react-router-dom"
import logo from "../images/logo.png"
import "./styles/Navbar.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
class NavBar extends Component {
    confirmLogOut() {
        confirmAlert({
            title: 'Cerrar Sesin?',
            buttons: [
                {
                    label: 'Si, Cerrar',
                    onClick: async () => {
                        this.props.handleClick()
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    render() {
        return (
            <>
                {this.props.user ?
                    <Navbar collapseOnSelect bg="dark" expand="lg" variant="dark" sticky="top">
                        <Navbar.Brand><img className="navbar-brand d-none d-sm-block logo" src={logo} alt="logo" /></Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto ml-auto">
                                <NavDropdown className="d-flex align-items-center" title="Productos">
                                    <NavDropdown.Item>
                                        <Link to="/Productos">Stock</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Item>
                                        <Link to="/Mecanizados">Mecanizados</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Item>
                                        <Link to="/No-Mecanizados">No Mecanizados</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Item>
                                        <Link to="/Surtidores">Surtidores</Link>
                                    </NavDropdown.Item>
                                </NavDropdown>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Pedidos">Pedidos</Link>
                                </Nav.Link>
                                <NavDropdown className="d-flex align-items-center" title="Fichero">
                                    <NavDropdown.Item>
                                        <Link to="/Clientes">Clientes</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Item>
                                        <Link to="/Proovedores">Proovedores</Link>
                                    </NavDropdown.Item>
                                    <NavDropdown.Item>
                                        <Link to="/Expresos">Expresos</Link>
                                    </NavDropdown.Item>
                                </NavDropdown>
                                <Nav.Link>
                                    <Link className="nav-link" to="/Deudas">Deudas</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link className="nav-link" to="/ListasDePrecios">Listas De Precios</Link>
                                </Nav.Link>
                                {this.props.user.email === "gomezbonardi@hotmail.com" ?
                                    <Nav.Link>
                                        <Link className="nav-link" to="/Cheques">Cheques</Link>
                                    </Nav.Link>
                                    :
                                    null
                                }
                            </Nav>
                        </Navbar.Collapse>
                        <Nav.Link>
                            <button type="button" className="btn btn-outline-danger" onClick={e => {
                                this.confirmLogOut()
                            }}>Log Out</button>
                        </Nav.Link>
                        <Link className="navbar-brand p-2 btn btn-success" to="/NuevoPedido" id="buttonNuevoPedido">Nuevo Pedido</Link>
                    </Navbar>
                    :
                    null
                }
            </>
        )
    }
}
export default NavBar