import React, { Component } from "react"
import { Link } from "react-router-dom"
import Login from "../components/Login"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import FichaProovedor from "../components/FichaProovedor"
import "./Styles/ProovedoresList.css"
import { connect } from "react-redux"
import { changeProveedor } from "../actions/index"

class ProovedoresList extends Component {
    handleChange(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        console.log(arrayClientes)
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.parentNode.style.display = "none";
            else
                f.parentNode.parentNode.style.display = "block";
        });
    }
    returnBack = () => {
        this.props.changeProveedor(null)
    }
    render() {
        try {
            const user = this.props.user.email
            if (user) {
                if (this.props.proveedor) {
                    const proveedor = this.props.proveedores[this.props.proveedor]
                    return (
                        <div>
                            <FichaProovedor proovedor={proveedor} seccion="proovedores" handleClick={e => {
                                this.returnBack()
                            }} />
                        </div>
                    )
                }
                else {
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChange(e)
                                                            }
                                                        } placeholder="Buscar Proveedor" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.values(this.props.proveedores).map(proovedor => (
                                            <div className="col-12 col-md-6 col-lg-4 text-center form-group">
                                                <Link to={{
                                                    pathname: `/proovedores/`,
                                                    search: `${proovedor.datos.nombre}`
                                                }}>
                                                    <button type="button" className="btn btn-outline-light btn-cliente" onClick={() => {
                                                        this.props.changeProveedor(proovedor.datos.nombre)
                                                    }}>
                                                        {proovedor.datos.nombre}
                                                    </button>
                                                </Link>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch{
            return (
                <Login redirect="Proovedores" />
            )
        }
    }
}
const mapStateToProps = state => ({
    user: state.user,
    proveedores: state.data["proovedores2"],
    proveedor: state.proveedor
})
const mapDispatchToProps = {
    changeProveedor,

}
export default connect(mapStateToProps, mapDispatchToProps)(ProovedoresList)