import GilbarcoDobleLegacy from "../images/GilbarcoDobleLegacy.png"
import WayneDobleCentury from "../images/WayneDobleCentury.png"
import WayneDobleDL1 from "../images/WayneDobleDL1.png"
import WayneSimpleCentury from "../images/WayneSimpleCentury.png"
import WayneSimpleDL1 from "../images/WayneSimpleDL1.png"
import Evertite3Pulg from "../images/Evertite3Pulg.png"
import Evertite4Pulg from "../images/Evertite4Pulg.png"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import { connect } from "react-redux"
class NuevoPedido extends Component {
    seleccionarDestinatario(cliente) {
        this.setState({
            nuevoCliente: cliente
        })
    }
    async enviarPedido() {
        let arr = []
        Object.keys(this.state.pedido).map(key => {
            arr.push(
                {
                    nombre: this.state.pedido[key].nombre,
                    cantidad: this.state.pedido[key].cantidad,
                    seccion: this.state.pedido[key].seccion
                }
            )
        })
        console.log(arr)
        firebase.database().ref().child("pedidos2").push(
            {
                cliente: this.state.nuevoCliente,
                precio: this.state.total,
                articulos: arr
            }
        )



    }
    confirm() {
        confirmAlert({
            title: 'Agregar Pedido?',
            buttons: [
                {
                    label: 'Agregar',
                    onClick: async () => {
                        this.enviarPedido().then(
                            this.setState({
                                redirect: true
                            })
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    }
    state = {
        nuevoCliente: undefined,
        total: 0,
        pedido: [],
    }
    agregarArticulo(nombre, cantidad, precio, seccion) {
        const arr = this.state.pedido
        arr.push({
            nombre: nombre,
            cantidad: cantidad,
            precio: precio,
            seccion: seccion
        })
        super.setState({
            ...this.state,
            pedido: arr
        })
    }
    handleChangeProducto(e) {
        var forEach = Array.prototype.forEach;
        var array = document.getele
        var arrayProductos = document.getElementsByClassName("col-6 col-md-3 col-lg-2 form-group text-center align-items-center")
        forEach.call(arrayProductos, function (f) {
            if (f.children[0].children[1].children[0].children[0].children[0].children[0].innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1) {
                f.className = "col-6 col-md-3 col-lg-2 form-group text-center d-none align-items-center"
                console.log(f.className)
            }
            else {
                f.className = "col-6 col-md-3 col-lg-2 form-group text-center d-flex align-items-center"
            }
        });
    }
    handleChangeCliente(e) {
        var forEach = Array.prototype.forEach;
        var arrayClientes = document.getElementsByClassName("btn-cliente")
        forEach.call(arrayClientes, function (f) {
            if (f.innerHTML.toLowerCase().search(e.target.value.toLowerCase()) == -1)
                f.parentNode.style.display = "none";
            else
                f.parentNode.style.display = "block";
        });
    }
    modificarTotal(total) {
        console.log(total)
        this.setState({ total: total })
    }
    render() {
        try {
            let user = undefined
            user = this.props.user.email
            if (user) {
                if (!this.state.nuevoCliente) {
                    return (
                        <div className="nuevo-cliente bg-dark">
                            <div className="container-fluid mt-4">
                                <div className="row">
                                    <div className="col-6 offset-3">
                                        <form>
                                            <div className="formRow">
                                                <div className="form-group col-12 text-center">
                                                    <input type="text" className="form-control" onChange={
                                                        e => {
                                                            this.handleChangeCliente(e)
                                                        }
                                                    } placeholder="Buscar Cliente" />
                                                </div>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                                <div className="row">
                                    {Object.values(this.props.clientes).map(cliente => (
                                        <div className="col-12 text-center form-group">
                                            <button type="button" className="btn btn-primary btn-cliente" onClick={() => {
                                                console.log(cliente.datos.nombre)

                                                this.seleccionarDestinatario(cliente.datos.nombre)
                                            }}>
                                                {cliente.datos.nombre}
                                            </button>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    )
                }
                else {
                    console.log(this.props.products)
                    return (
                        <div>
                            <div className="productList">
                                <div className="container-fluid">
                                    <div className="row">
                                        <div className="col-12 text-center">
                                            <h1 className="name">Elegir Productos</h1>
                                        </div>
                                    </div>
                                    <div className="row">
                                        <div className="col-6 offset-3">
                                            <form>
                                                <div className="formRow">
                                                    <div className="form-group col-12 text-center">
                                                        <input type="text" className="form-control" onChange={
                                                            e => {
                                                                this.handleChangeProducto(e)
                                                            }
                                                        } placeholder="Buscar Producto" />
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                    <div className="row">
                                        {Object.keys(this.props.products).map(key => (
                                            <div className="col-6 col-md-3 col-lg-2 form-group text-center d-flex align-items-center" key={this.props.products[key].nombre}>
                                                <ProductElection handleClick={e => {
                                                    console.log(this.props.products[key])
                                                    const cantidad = prompt(this.props.products[key].nombre)
                                                    if (cantidad) {
                                                        this.agregarArticulo(this.props.products[key].nombre, cantidad, this.props.products[key].precio, "productos")
                                                        if (this.props.products[key].precio) {
                                                            let sumaPrecios
                                                            if (this.state.total) {
                                                                sumaPrecios = this.state.total
                                                            }
                                                            else {
                                                                sumaPrecios = 0
                                                            }
                                                            sumaPrecios += parseInt((this.props.products[key].precio) * cantidad)
                                                            console.log(sumaPrecios)
                                                            this.setState({
                                                                total: sumaPrecios
                                                            })
                                                        }
                                                    }
                                                }} nombre={this.props.products[key].nombre} precio={this.props.products[key].precio} imagen={this.props.images[this.props.products[key].nombre]} />
                                            </div>
                                        ))}
                                    </div>

                                    <div className="row">
                                        {Object.keys(this.props.surtidores).map(key => (
                                            <div className="col-6 col-md-3 col-lg-2 form-group text-center d-flex align-items-center" key={this.props.surtidores[key].nombre}>
                                                <ProductElection handleClick={e => {
                                                    const cantidad = prompt(this.props.surtidores[key].nombre)
                                                    if (cantidad) {
                                                        this.agregarArticulo(this.props.surtidores[key].nombre, cantidad, this.props.surtidores[key].precio, "Surtidores")
                                                        if (this.props.surtidores[key].precio) {
                                                            let sumaPrecios
                                                            if (this.state.total) {
                                                                sumaPrecios = this.state.total
                                                            }
                                                            else {
                                                                sumaPrecios = 0
                                                            }
                                                            sumaPrecios += parseInt((this.props.surtidores[key].precio) * cantidad)
                                                            console.log(sumaPrecios)
                                                            this.setState({
                                                                total: sumaPrecios
                                                            })
                                                        }
                                                    }
                                                }} nombre={this.props.surtidores[key].nombre} precio={this.props.surtidores[key].precio} imagen={this.props.images[this.props.surtidores[key].nombre]} />
                                            </div>
                                        ))}
                                    </div>
                                    <div className="row">
                                        <ResumenPedido
                                            cliente={this.state.nuevoCliente}
                                            pedido={this.state.pedido}
                                            enviarPedido={e => {
                                                this.confirm()
                                            }}
                                            modificarTotal={total => {
                                                this.modificarTotal(total)
                                            }}
                                            total={this.state.total}
                                        />
                                    </div>
                                    {this.state.redirect ?
                                        <Redirect to="pedidos" />
                                        :
                                        null
                                    }
                                </div>
                            </div>
                        </div>
                    )
                }
            }
        }
        catch (error) {
            return (
                <Login redirect="NuevoPedido" />
            )
        }
    }
}
const mapStateToProps = state => ({
    user: state.user,
    images: state.images,
    products: state.data["productos2"],
    clientes: state.data["clientes2"],
    surtidores: state.data["Surtidores2"],
})
export default connect(mapStateToProps, null)(NuevoPedido)