import React, { Component, Fragment } from "react"
import NavBar from "./NavBar"
import SpinnerLoading from "../components/SpinnerLoading"
import * as firebase from "firebase"
import Login from "../components/Login"
import {
    getData,
    getDocumentos,
    changeUser,
    isLoading,
    recuperandoUsuario
} from "../actions/index"
import { connect } from "react-redux"


class Layout extends Component {
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    logOut = async () => {
        await firebase.auth().signOut()
        window.location.reload()
    }
    async authListener() {
        await firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                this.props.changeUser(user)
            }
            this.props.recuperandoUsuario(false)
        })
    }
    async fetchData() {
        const productosRef = await firebase.database().ref()
        productosRef.on("value", snapshot => {
            this.props.getData(snapshot.val())
            console.log(snapshot.val())
            this.props.isLoading(false)
        })
    }
    async componentDidMount() {
        await this.authListener()
        await this.fetchData()
    }
    render() {
        return (
            <>
                {
                    this.props.recuperarUsuario ?
                        <>
                            <SpinnerLoading title="Recuperando Usuario..." />
                        </>
                        :
                        <div className="app">
                            {!this.props.user ?
                                <Login />
                                :
                                <>
                                    {this.props.loading ?
                                        <SpinnerLoading title="Recuperando Informacion..." />
                                        :
                                        <>
                                            <NavBar user={this.props.user} handleClick={this.logOut} />
                                            {this.props.children}
                                        </>
                                    }
                                </>
                            }
                        </div>
                }
            </>
        )
    }
}
const mapStateToProps = state => ({
    recuperarUsuario: state.recuperarUsuario,
    loading: state.loading,
    user: state.user,
    documentos: state.documentos
})
const mapDispatchToProps = {
    getData,
    getDocumentos,
    changeUser,
    isLoading,
    recuperandoUsuario
}
export default connect(mapStateToProps, mapDispatchToProps)(Layout)